.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn {
  margin: 5px;
  width: 100px;
  height: 25px;
  background-color: slategrey;
  color: ivory;
  font-weight: bold;
  border: none;
  border-radius: 3px;
}

#date-picker {
  border-radius: 3px;
}

.img-card {
  width: 80%;
  margin: 0 auto;
}

.apod {
  max-width: 100%;
}
